<template>
  <div>
    <separator></separator>
    <div>
      <el-button type="primary" @click="adddDataFrom">新增</el-button>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        type="index" label="序号">
      </el-table-column>
      <el-table-column
        prop="name"
        label="菜单名称">
      </el-table-column>
      <el-table-column
        prop="code"
        label="编码">
      </el-table-column>
      <el-table-column
        prop="menuCode"
        label="菜单编码">
      </el-table-column>
      <el-table-column
        prop="parentName"
        label="父级菜单">
      </el-table-column>
      <el-table-column
        prop="perms"
        label="权限注解">
      </el-table-column>
      <el-table-column label="类型" width="180">
        <template slot-scope="scope">
          <el-button v-if="scope.row.type == 0" type="text" size="small">目录</el-button>
          <el-button v-if="scope.row.type == 1" type="text" size="small">菜单</el-button>
          <el-button v-if="scope.row.type == 2" type="text" size="small">按钮</el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="icon"
        label="图标">
      </el-table-column>
      <el-table-column
        prop="orderNum"
        label="排序">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="updateopen(scope.row)">编辑</el-button>
          <el-button v-if="scope.row.code != '1003'" type="text" size="small" @click="deleteopen(scope.row)">删除</el-button>
          <el-button v-if="scope.row.type != 2" type="text" size="small"
                     @click="addopen(scope.row)">新增
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <div class="block">
      <el-pagination layout="total,sizes,prev, pager, next" :total="total" :page-size="pageSize" :page-sizes="[5,10,20]"
                     @size-change="handleSizeChange" :current-page="currentPage" @current-change="handleCurrentChange">
      </el-pagination>
    </div>

    <el-dialog title="新增菜单" :visible.sync="adddialogVisible" width="30%" :before-close="createFileHandleClose">
      <el-form ref="createfrom" :model="createfrom" :rules="rules" label-width="80px">
        <el-form-item label="名称" prop="name">
          <el-input v-model="createfrom.name"></el-input>
        </el-form-item>
        <el-form-item label="菜单编码" prop="menuCode">
          <el-input v-model="createfrom.menuCode"></el-input>
        </el-form-item>
        <el-form-item label="权限注解" prop="perms">
          <el-input v-model="createfrom.perms"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon">
          <el-input v-model="createfrom.icon"></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="orderNum">
          <el-input v-model="createfrom.orderNum"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-select v-if="socperow.type == 0" v-model="createfrom.type = typeoption[1].value">
            <el-option :key="typeoption[1].value" :label="typeoption[1].label" :value="typeoption[1].value">
            </el-option>
          </el-select>
          <el-select v-if="socperow.type == 1" v-model="createfrom.type = typeoption[2].value">
            <el-option :key="typeoption[2].value" :label="typeoption[2].label" :value="typeoption[2].value">
            </el-option>
          </el-select>
          <el-select v-if="socperow.type == null" v-model="createfrom.type = typeoption[0].value">
            <el-option :key="typeoption[0].value" :label="typeoption[0].label" :value="typeoption[0].value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上级菜单" prop="parentCode">
          <el-select v-model="createfrom.parentCode = socperow.code">
            <el-option :key="socperow.code" :label="socperow.name" :value="socperow.code">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="sub">
          <el-button type="primary" @click="cancelcreatedialogVisible">取 消</el-button>
          <el-button type="primary" @click="createdFrom('createfrom')">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="编辑菜单" :visible.sync="updatedialogVisible" width="30%" :before-close="updateFileHandleClose">
      <el-form ref="updatefrom" :model="updatefrom" :rules="rules" label-width="80px">
        <el-form-item label="名称" prop="name">
          <el-input v-model="updatefrom.name"></el-input>
        </el-form-item>
        <el-form-item label="菜单编码" prop="menuCode">
          <el-input v-model="updatefrom.menuCode"></el-input>
        </el-form-item>
        <el-form-item label="权限注解" prop="perms">
          <el-input v-model="updatefrom.perms"></el-input>
        </el-form-item>
        <el-form-item label="图标" prop="icon">
          <el-input v-model="updatefrom.icon"></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="orderNum">
          <el-input v-model="updatefrom.orderNum"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-if="updatefrom.type == 0" placeholder="目录" :disabled="true"></el-input>
          <el-input v-if="updatefrom.type == 1" placeholder="菜单" :disabled="true"></el-input>
          <el-input v-if="updatefrom.type == 2" placeholder="按钮" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="上级菜单" prop="parentCode">
          <el-input :placeholder="updatefrom.parentName" :aria-disabled="true"></el-input>
        </el-form-item>
        <el-form-item class="sub">
          <el-button type="primary" @click="cancelupdatedialogVisible">取 消</el-button>
          <el-button type="primary" @click="updateFrom">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import separator from "../../../../public/separator.vue";
import NetAPI from "../../../../../config/api.js";

export default {
  name: "menu_manage",
  components: {
    separator,
  },
  data () {
    return {
      createfrom: {
        parentCode: "",
        name: "",
        menuCode: "",
        perms: "",
        type: "",
        icon: "",
        orderNum: "",
      },
      rules: {
        orderNum: [
          { type: 'number', message: '排序必须为数字值', trigger: 'blur' }
        ],
      },
      updatefrom: {
        id: "",
        code: "",
        parentCode: "",
        parentName: "",
        name: "",
        menuCode: "",
        perms: "",
        type: "",
        icon: "",
        orderNum: "",
      },
      typelable: [0, 1, 2],
      typeoption: [{ value: 0, label: "目录" }, { value: 1, label: "菜单" }, { value: 2, label: "按钮" }],
      updateoption: [{ value: 0, label: "目录" }, { value: 1, label: "菜单" }, { value: 2, label: "按钮" }],
      tableData: [],
      pageSize: 10, //当前分页
      currentPage: 1, //当前页
      total: 0, //一共多少条数据
      socperow: {
        id: "",
        code: "",
        parentCode: "",
        parentName: "",
        name: "",
        menuCode: "",
        perms: "",
        type: "",
        icon: "",
        orderNum: "",
      },
      adddialogVisible: false,
      updatedialogVisible: false,
    }
  },
  created () {
    this.netAPI = NetAPI().get("menu_allocation");
    this.$http.get(this.netAPI.find, {
      params: { pageNo: this.currentPage, pageSize: this.pageSize }
    }).then(
      res => {
        if (res.body.status == 200) {
          this.total = res.body.total;
          this.tableData = res.body.data;
        }
      }, err => {
      }
    );
  },
  methods: {
    createFileHandleClose () {
      this.adddialogVisible = false;
      this.$refs.createfrom.resetFields();
    },
    updateFileHandleClose () {
      this.updatedialogVisible = false;
      this.$refs.updatefrom.resetFields();
    },
    cancelcreatedialogVisible () {
      this.$refs.createfrom.resetFields();
      this.adddialogVisible = false;
    },
    cancelupdatedialogVisible () {
      this.$refs.updatefrom.resetFields();
      this.updatedialogVisible = false;
      this.getDataList();
    },
    addopen (row) {
      this.socperow = row;
      this.adddialogVisible = true;
    },
    updateopen (row) {
      this.updatefrom = row;
      console.log(this.updatefrom);
      this.updatedialogVisible = true;
    },
    adddDataFrom () {
      this.socperow.type = null;
      this.socperow.code = "1003";
      this.socperow.name = "菜单根节点";
      this.adddialogVisible = true;
    },
    handleSizeChange (pageSize) {
      //设置当前显示条数
      this.pageSize = pageSize;
      this.getDataList();
    },
    handleCurrentChange (current) {
      //根据当前页面获取数据
      this.currentPage = current;
      this.getDataList();
    },
    updateFrom () {
      this.$http.post(this.netAPI.update, this.updatefrom).then(
        res => {
          this.$refs.updatefrom.resetFields();
          this.updatedialogVisible = false;
          if (res.body.status == 200) {
            this.getDataList();
          } else {
            this.$message.warning(
              "服务错误" + res.body.message
            );
          }
        }, err => {
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
        }
      );
    },
    createdFrom (formName) {
      this.$http.post(this.netAPI.add, this.createfrom).then(
        res => {
          this.$refs.createfrom.resetFields();
          this.adddialogVisible = false;
          if (res.body.status == 200) {
            this.getDataList();
          } else {
            this.$message.warning(
              "服务错误" + res.body.message
            );
          }
        }, err => {
          this.$message({
            message: "服务错误" + err,
            type: "warning"
          });
        }
      );
    },
    deleteopen (row) {
      this.$confirm("是否删除该菜单?", "删除菜单", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http.get(this.netAPI.delete, { params: { id: row.id } }).then(
            res => {
              if (res.body.status == 200) {
                this.getDataList();
              } else {
                this.$message.warning(
                  "服务错误" + res.body.message
                );
              }
            }, err => {
              this.$message({
                message: "服务错误" + err,
                type: "warning"
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作"
          });
        });
    },
    getDataList () {
      this.$http.get(this.netAPI.find, { params: { pageSize: this.pageSize, pageNo: this.currentPage } }).then(
        res => {
          if (res.body.status == 200) {
            this.tableData = res.body.data;
            this.total = res.body.total;
          }
        }, err => {

        }
      );
    }
  }
}
</script>

<style lang="scss" scoped>
.sub {
  text-align: right;
}
</style>



